














	export default {
		data() {
			return {
				name: '',
				nameEN: '',
				location: '',
				tel: '',
				fax: '',
                hasOrder: false
			}
		},
		onLoad(e) {
			uni.showLoading();
			uni.request({
				url: this.$requestUrl+'get_company_info',
				method: 'GET',
				success: res => {
					let info = res.data.data;
					this.name = info.company_name;
					this.nameEN = info.company_name_en;
					this.location = info.company_location;
					this.tel = info.company_tel;
					this.fax = info.company_fax;
				},
				fail: () => {},
				complete: () => {
					uni.hideLoading();
				}
			});
            uni.login({
            	provider: 'weixin',
            	success: res => {
                    uni.request({
                    	url: this.$requestUrl+'code_2_session',
                    	method: 'GET',
                    	data: {
                            js_code: res.code
                        },
                    	success: res => {
                            if (res.data.status == 1) {
                            	this.hasOrder = true;
                            }
                        }
                    });
                }
            });
		},
		onShareAppMessage() {
			return {
				title: '天津鼎盛润泽商贸有限公司',
				path: '/pages/index/index'
			}
		},
		methods: {
			makePhoneCall: function(){
				uni.makePhoneCall({
					phoneNumber: this.tel,
				})
			},
            toOrder() {
                if (this.hasOrder) {
                	uni.navigateTo({
                		url: '../order/order'
                	});
                }
            }
		}
	}



